import '@kor-ui/kor';
import '@kor-ui/kor/kor-styles.css';
import {html} from 'lit-html';

/* This module is developed to work with the Closure Compiler and JSDoc
 * providing types checking, code optimisation and automatic generation of documentation,
 * for the details see please:
 * https://jsdoc.app/tags-type.html
 * https://github.com/google/closure-compiler
 */

export default {
  /* Generates the tab strip with the data binding.
   * Each tab, when activated, changes a visibility of an appropriate content
   * @func tab_strip_view
   * @param {Object} summary_content
   * @param {Object} transactions_content
   * @param {Object} transactions_content
   */
  tab_strip_view(summary_content, transactions_content, schedule_content) {
    return html`
      <kor-tab-item id="summaryTab" label="Summary" active
        @active-changed="${(e)=>{
          if (e.target.active) {
            summary_content.style.display = "block";
            transactions_content.style.display = "none";
            schedule_content.style.display = "none";
          }
        }}">
      </kor-tab-item>
      <kor-tab-item id="transactionsTab" label="Transactions"
        @active-changed="${(e)=>{
          if (e.target.active) {
            summary_content.style.display = "none";
            transactions_content.style.display = "block";
            schedule_content.style.display = "none";
          }
        }}">
      </kor-tab-item>
      <kor-tab-item id="scheduleTab" label="Schedule"
        @active-changed="${(e)=>{
          if (e.target.active) {
            summary_content.style.display = "none";
            transactions_content.style.display = "none";
            schedule_content.style.display = "block";
          }
        }}">
      </kor-tab-item>`;
  },
  /* Generate a table with two columns from Object.entries()
   * array of [key, value] items
   * @func object_entries_two_columns_view
   * @param {Array.<string, (string|number)>} object_entries_array
   */
  object_entries_two_columns_view(object_entries_array) {
    return html`
      <kor-table columns="250px auto">
        <kor-table-row slot="header">
          <kor-table-cell head>Key</kor-table-cell>
          <kor-table-cell head>Value</kor-table-cell>
        </kor-table-row>
        ${object_entries_array.map(
          (key_value) => html`
            <kor-table-row>
              <kor-table-cell>${key_value[0]}</kor-table-cell>
              <kor-table-cell>${key_value[1]}</kor-table-cell>
            </kor-table-row>`)}
      </kor-table>`;
  },
  /* Generates a transactions view
   *
   * @callback transaction_details_callback
   * @param {Object} transaction
   *
   * @func transactions_view
   * @param {Array.<Object>} in_transactions
   * @param {Array.<Object>} out_transactions
   * @param {transaction_details_callback} transaction_details
   */
  transactions_view(in_transactions, out_transactions, transaction_details) {
    return html`
      <kor-grid columns="2">
        <kor-card grid-cols="1" grid-cols-m="2" grid-cols-s="2" flat>
          <kor-text size="header-1">Incoming transactions</kor-text>
          <kor-table columns="1fr auto">
            <kor-table-row slot="header">
              <kor-table-cell head>From address</kor-table-cell>
              <kor-table-cell head>Amount in Wei</kor-table-cell>
            </kor-table-row>
            ${in_transactions.map(
              (transaction) => html`
                <kor-table-row @click="${(e)=>{transaction_details(transaction)}}">
                  <kor-table-cell>${transaction.attributes.from_address}</kor-table-cell>
                  <kor-table-cell>${Number.parseInt(transaction.attributes.value)}</kor-table-cell>
                </kor-table-row>`
            )}
          </kor-table>
        </kor-card>
        <kor-card grid-cols="1" grid-cols-m="2" grid-cols-s="2" flat>
          <kor-text size="header-1">Out coming transactions</kor-text>
          <kor-table columns="1fr auto">
            <kor-table-row slot="header">
              <kor-table-cell head>To address</kor-table-cell>
              <kor-table-cell head>Amount in Wei</kor-table-cell>
            </kor-table-row>
            ${out_transactions.map(
              (transaction) => html`
                <kor-table-row @click="${(e)=>{transaction_details(transaction)}}">
                  <kor-table-cell>${transaction.attributes.to_address}</kor-table-cell>
                  <kor-table-cell>${Number.parseInt(transaction.attributes.value)}</kor-table-cell>
                </kor-table-row>`
            )}
          </kor-table>
        </kor-card>
      </kor-grid>`;
  }
}
