import '@kor-ui/kor';
import '@kor-ui/kor/kor-styles.css';
import {html, render} from 'lit-html';
import Templates from './templates.js';
import LogInOut from './loginlogout.js';

/* The DApp singleton
 * @const {Object}
 */
const DApp = (() => {
 // public:
  /* @const {Object}
   */
  const pub_ = {
    tmpl: document.getElementById('appTemplate').content
  };

 // private:
  const page = pub_.tmpl.querySelector("kor-page");
  const summary_content = pub_.tmpl.getElementById('summaryContent');
  const summary_details = pub_.tmpl.getElementById('summaryDetails');
  const transactions_content = pub_.tmpl.getElementById('transactionsContent');
  const schedule_content = pub_.tmpl.getElementById('scheduleContent');
  const bar = pub_.tmpl.querySelector("kor-app-bar");
  const app_menu = pub_.tmpl.getElementById('appMenu');
  const app_menu_popover = pub_.tmpl.getElementById('appMenuPopover');
  const app_menu_tooltip = pub_.tmpl.getElementById('appMenuTooltip');
  const login_menu = pub_.tmpl.getElementById('loginMenuItem');
  const prefs_menu = pub_.tmpl.getElementById('prefsMenuItem');
  const add_transaction = pub_.tmpl.getElementById('addTransaction');
  const add_transaction_modal = pub_.tmpl.getElementById('addTransactionModal');
  const add_transaction_tooltip = pub_.tmpl.getElementById('addTransactionTooltip');
  const transaction_details_modal = pub_.tmpl.getElementById('transactionDetailsModal');
  const tab_strip = pub_.tmpl.querySelector("kor-tabs");
  //const viewport_width = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0);
  //const viewport_height = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0);

  // Render tabs into the tab strip
  render(Templates.tab_strip_view(summary_content, transactions_content, schedule_content), tab_strip);
  const summary_tab = pub_.tmpl.getElementById('summaryTab');
  const transactions_tab = pub_.tmpl.getElementById('transactionsTab');
  const schedule_tab = pub_.tmpl.getElementById('scheduleTab');

  // Check whether the dark theme should be applied
  if (page && window.matchMedia
      && window.matchMedia('(prefers-color-scheme: dark)').matches) {
    page.setAttribute("theme", "dark");
  }

  // The app settings icon clicking
  add_transaction.addEventListener('click', () => {
    add_transaction_tooltip.setAttribute("visible", "false");
    add_transaction_modal.setAttribute("visible", "true");

  });

  app_menu.addEventListener('click', () => {
    app_menu_tooltip.setAttribute("visible", "false");
  });

  login_menu.setAttribute("login", "true");
  /* @func loginlogout - An instantiated functional object
   */
  const loginlogout =
    LogInOut.Init(summary_details, transactions_content, schedule_content,
      login_menu, add_transaction, transaction_details_modal);
  login_menu.addEventListener('click', () => {
    // Close the app menu
    app_menu_popover.removeAttribute("visible");
    loginlogout();
  });

  // Populate the public object
  return pub_;
})(); // The DApp singleton

document.addEventListener('DOMContentLoaded', () => {
  // Moralis is bound in the app.html via the script tag,
  // because it is better not to include it into the ./node_modules,
  // as it has one package with low vulnerability (insecure credential storage),
  // a legacy cjs modules system and huge number of dependencies with duplicated code
  Moralis.initialize("pnj2ou0ivNkr2xnWIQIKZjwhcbJgC6hO69CasCTS" /* YOUR_APP_ID */);
  Moralis.serverURL = 'https://kcmeib0ovv6c.moralis.io:2053/server';

  // Move the app from the document fragment into the page's DOM,
  // when the styles are parsed
  const body = document.getElementsByTagName('body')[0];
  body.removeChild(document.getElementById('startupContent'));
  body.appendChild(DApp.tmpl);
}); // DOMContentLoaded
